import React, {Component} from 'react';

class Navhead extends Component {
    render() {
        return (
            <div className="bg-white flex h-12 w-full box-border px-6 fixed top-0 right-0 border z-10">
                <div className="flex-none  font-black subpixel-antialiased flex ">
                    <span className="text-3xl content-center">zhangxurui</span>
                </div>
                <div className="flex-1"></div>
                <div className="flex-none">
                    {/*通过设置高和款控制svg画布的大小，path标签可以在svg画布上进行路径绘画，这里绘制了三条直线
                     其中M x y表示从画布上的（x，y）位置开始画，h l表示话一条长度为l的水平直线。画布的原点在左上角，向右为x的正半轴，向下为y轴的正半轴。
                     教程地址：https://cloud.baidu.com/article/3294183
                     再通过视窗来控制显示的svg画布大小 四个参数来指定坐标系原点的位置以及它的尺寸：x y width height
                     width height小于svg的长宽时为截取svg的部分再放大到指定长款。如果width height大于svg的长宽时，为将svg放入大视窗画布中，再将视窗整体缩小至svg大小。
                     如果想缩小svg且svg还在正中间，就要调整svg在视窗的正中心，即指定viewbox中的x和y。
                     */}
                    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 48 48" strokeWidth={1.5}
                         stroke="currentColor" className="w-12 h-12 box-border">
                        <path strokeLinecap="round" strokeLinejoin="round" d="M10 16h24 M10 24h24 M10 32 h24"/>
                    </svg>
                </div>
            </div>
        );
    }
}

export default Navhead;