import React, {Component} from 'react';
import "./index.css"
import Mark from "./mark";
import {Route} from "react-router-dom";

class ShowText extends Component {

    render() {

        return (
            <div id="showtext" className="h-full w-4/5 box-border pt-12 pl-4 fixed top-0 right-0">
                <Route path="/about_me" component={Mark}></Route>
                <Route path="/array_method" component={Mark}></Route>
                <Route path="/Scope" component={Mark}></Route>
                <Route path="/closure" component={Mark}></Route>
            </div>
        );
    }
}

export default ShowText;